:root {
    --color-orange: #cda663;
    --color-blue: #3199dc;
    --color-dark: #222222;
    --color-light: #f8f9fa;
    --color-white: #fff;
    --color-black: black;

    --spacing-small: 5px;
    --spacing-sm: 10px;
    --spacing-medium: 15px;
    --spacing-large: 20px;

    --box-shadow: 0 2px 24px rgb(000,000,000, 0.5);
}

*, ::after, ::before {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    background-color: var(--color-orange);
    font-size: 16px;
    line-height: 1.7;
    font-family: Arial, Helvetica, sans-serif;
}

* {
    scrollbar-color: var(--color-blue) var(--color-dark);
    scrollbar-width: thin;
}
::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}
::-webkit-scrollbar-thumb {
    background-color: var(--color-blue);
    border-radius: 2px;
}
::-webkit-scrollbar-track {
    background-color: var(--color-dark);
}

.img-cover{
    display: block;
    object-fit: cover;
    object-position: center;
}

.img-contain{
    display: block;
    object-fit: contain;
    object-position: center;
}

.btn{
    border: none;
    outline: none;
    background: none;
    cursor: pointer;
}

.bg-contain{
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
}

.btnForCreateNote{
    position: absolute;
    top: 0;
    left: 90px;
    width: max-content;
    height: min-content;
    background-color: rgb(13, 153, 29);
    padding:var(--spacing-sm) var(--spacing-medium);
    border-radius: 5px;
    color: var(--color-white);
    font-size: 16px;
    transition: box-shadow .2s ease-in;
}

.btnForCreateNote:hover{
    box-shadow: var(--box-shadow);
}